
import { defineComponent, reactive, ref } from "vue";
import { BranchDeptInfo } from "@/shared/type/COM20200111.type";
import { COM20200121_REQ_BODY, COM20200121_RES } from "@/shared/type/COM20200121.type";
import { COM20200131_RES_BODY, COM20200131_RES } from "@/shared/type/COM20200131.type";
import { COM20200112_REQ_BODY, COM20200112_RES } from "@/shared/type/COM20200112.type";
import VueDatePicker from '@vuepic/vue-datepicker';
import { modalController } from "@ionic/vue";
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue'
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue'
import { screenMode } from "@/shared/type/Approval.type";
import { APM20200113_RES } from "@/shared/type/APM20200113.type";

export default defineComponent({
  name: "com202001sc01",
  components: {
    SelectOption,
    VueDatePicker,
    BreadCrumb,
    ModifyCard
  },
  data() {
    const branchDeptData = ref<BranchDeptInfo[]>([])
    const viewType = this.$route.query.type
    const input = reactive({
      branchCode: "",
      branchName: "",
      branchShortName: "",
      branchStatusCode: "",
      branchTypeCode: "",
      firstRegisterTellerName: "",
      firstRegisterTellerID: "",
      firstRegisterBranchCode: "",
      firstRegisterDate: "",
      firstRegisterTime: "",
      lastChangeTellerName: "",
      lastChangeTellerID: "",
      lastChangeBranchCode: "",
      lastChangeDate: "",
      lastChangeTime: ""
    });
    const modifyCard = {
      registerTellerId: '',
      registerTellerName: '',
      registerDate: '',
      registerTime: '',
      updatedTellerId: '',
      updatedTellerName: '',
      updatedDate: '',
      updatedTime: '',
    }
    return {
      approvalRes: {} as any,
      operationMode: 'normal' as screenMode,
      isDisabledBranchStatus: true,
      modifyCard,
      isInValidInput: false,
      input,
      isDisabledField: ref(true),
      isDisabled: ref(false),
      isEdited: ref(false),
      branchDeptData,
      viewType,
      breadcrumbs: [
        { value: 'apm201001sc00.label.home', keyPath: '/COM200001SC00', type: 'route' },
        { value: 'com203002sc00.portal_management', keyPath: '', type: 'label' },
        { value: 'com202001sc00.branch_dept_list', keyPath: '/COM202001SC00', type: 'route' },
        { value: `${viewType === 'edit' ? 'com202001sc01.branch_dept_edit' : 'com202001sc01.branch_dept_register'}`, keyPath: '', type: 'label' },
      ]
    };
  },
  computed: {
    approvalType(): string {
      return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
    },
    branchStatusCode() {
      return this.$store.state.CommonCodeModule.commonCode.BranchStatusCode
    },
    branchTypeCode() {
      return this.$store.state.CommonCodeModule.commonCode.branchType
    },
  },
  async mounted() {
    if (this.approvalType === '') {
      if (this.viewType == 'edit') {
        this.handleFetchBranchDetail();
        this.isDisabled = true
        this.isEdited = true
      } else {
        this.input.branchStatusCode = this.branchStatusCode[0].key
        this.input.branchTypeCode = this.branchTypeCode[0].key
        this.isDisabledField = false
        this.isDisabled = false
        this.isEdited = false
      }
    } else {
      await this.$approval.getAuthorizationDetail().then((authDetail: APM20200113_RES) => {
        let authDetailParsed = JSON.parse(authDetail.body.requestContent)
        this.approvalRes = authDetail.body
        this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
        this.input.branchCode = authDetailParsed.branchCode
        this.input.branchName = authDetailParsed.branchName
        this.input.branchShortName = authDetailParsed.branchShortName
        this.input.branchStatusCode = authDetailParsed.branchStatusCode
        this.input.branchTypeCode = authDetailParsed.branchTypeCode
      })
    }
  },
  methods: {
    handleClickAction(actionType: string) {
      switch (actionType) {
        case 'save':
          if (this.viewType == 'edit') {
            this.handleUpdateBranchData()
            this.isDisabled = true
            this.isEdited = true
          } else {
            this.handleRegisterBranchData()
            this.isDisabled = false,
              this.isEdited = false

          }
          break;
        case "cancel":
          if (this.viewType == "edit") {
            this.handleFetchBranchDetail()
            this.isDisabled = true;
            this.isEdited = true
          } else {
            this.$router.push("/COM202001SC00")
          }
          break;
        default:
      }
    },
    handleListButton() {
      this.$router.push("/COM202001SC00");
    },

    handleEditButton() {
      if (this.viewType == "edit") {
        this.isDisabled = false;
        this.isDisabledBranchStatus = false;
        this.isEdited = false
      } else {
        this.isDisabled = true;
        this.isEdited = true
      }
    },
    // Save check condition Register and Edit 
    handleSaveButton() {
      this.isDisabledBranchStatus = true;
      if (this.input.branchCode && this.input.branchName && this.input.branchStatusCode && this.input.branchTypeCode) {
        this.$dialog.onOpen({
          content: this.$t("message.would_you_like_to_save"),
          dialogMode: 'Question',
          callback: () =>
            this.handleClickAction('save')
        })
      } else {
        this.isInValidInput = true
      }
    },
    handleCancelButton() {
      this.isDisabledBranchStatus = false
     this.$dialog.onOpen({
        content: this.viewType === 'edit' ? this.$t('message.cancel_without_saving_changes') : this.$t('message.are_you_sure_you_want_to_cancel_registration?'),
        dialogMode: 'Question',
        callback: () => {
          this.handleClickAction('cancel')
          modalController.dismiss()
        }
      });
    },
    // FetchBranchDetail (COM20200112) Done
    async handleFetchBranchDetail() {
      this.isDisabledBranchStatus = true;
      const trCode = "COM20200112"
      const bMock = false
      const body: COM20200112_REQ_BODY = {
        branchCode: `${this.$route.query.branchCode}`, 	 //BranchCode
      };
      const res = await this.$request.getRequest(trCode, body, bMock) as COM20200112_RES
      this.modifyCard = {
        registerTellerId: res?.body.firstRegisterTellerID,
        registerTellerName: res?.body.firstRegisterTellerName,
        registerDate: res?.body.firstRegisterDate,
        registerTime: res?.body.firstRegisterTime,
        updatedTellerId: res?.body.lastChangeTellerID,
        updatedTellerName: res?.body.lastChangeTellerName,
        updatedDate: res?.body.lastChangeDate,
        updatedTime: res?.body.lastChangeTime,
      }
      if (res.header.result) {
        return this.input = res?.body
      }
    },
    // Register (COM20200121) Done
    async handleRegisterBranchData() {
      modalController.dismiss()
      const trCode = "COM20200121"
      const bMock = false
      const body: COM20200121_REQ_BODY = {
        branchCode: this.input.branchCode, 	 //BranchCode
        branchName: this.input.branchName, 	 //BranchName
        branchShortName: this.input.branchShortName, 	 //BranchShortName
        branchStatusCode: this.input.branchStatusCode, 	 //BranchStatusCode
        branchTypeCode: this.input.branchTypeCode, 	 //BranchTypeCode
      }
      const res = await this.$request.getRequest(trCode, body, bMock) as COM20200121_RES
          if (res.header.result) {
            this.$router.push('/COM202001SC00')
            this.$dialog.onOpen({
              content: this.$t("message.register_success"),
              dialogMode: 'Success',
              callback: () => {
                modalController.dismiss()
          }
        });
      }
    },
    // Update (COM20200131) Done
    async handleUpdateBranchData() {
      modalController.dismiss()
      const trCode = "COM20200131"
      const bMock = false
      const body: COM20200131_RES_BODY = {
        branchCode: this.input.branchCode, 	 //BranchCode
        branchName: this.input.branchName, 	 //BranchName
        branchShortName: this.input.branchShortName, 	 //BranchShortName
        branchStatusCode: this.input.branchStatusCode, 	 //BranchStatusCode
        branchTypeCode: this.input.branchTypeCode 	 //BranchTypeCode
      }
      const res = await this.$request.getRequest(trCode, body, bMock) as COM20200131_RES
      if (res.header.result) {
        this.$dialog.onOpen({
          content: this.$t("message.update_success"),
          dialogMode: 'Success',
          callback: () => {
            this.handleFetchBranchDetail();
            modalController.dismiss()
          }
        })
        return this.input === res.body
      }
    }
  },
});
